
import React, { useState } from 'react';
import './SignInForm.css';
import './ResetPasswordForm.css';
import SignInButton from './SignInButton';
import ResetPassword from './ResetPasswordForm';
import { useNavigate } from 'react-router-dom'; // Import useNavigate for redirection      
import UserProfile  from './UserProfile';
import Cookies from 'js-cookie';
import { getAuth, signInWithEmailAndPassword } from "firebase/auth";

const SignInForm = ({ onClose }) => {
    const [formData, setFormData] = useState({
        email_address: '', 
        Password: '' 
    });
    const navigate = useNavigate(); // Hook for navigation
    const [error, setError] = useState(null);
    const [resetPasswordClicked, setResetPasswordClicked] = useState(false);

    const handleInputChange = (e) => {
        const { name, value } = e.target;
        setFormData({ ...formData, [name]: value });
    };

    const handleSubmit = async (e) => {
        e.preventDefault();

        try {
            const auth = getAuth(); // Initialize Firebase auth
            const userCredential = await signInWithEmailAndPassword(auth, formData.email_address, formData.Password);
            const user = userCredential.user;
            console.log('User signed in:', user);
            onClose(); // Close the form or redirect to another page
            Cookies.set('isLoggedIn', 'true', { expires: 7 }); // Set cookie on successful login
            // Handle the response based on your requirements
        } catch (error) {
            console.error('Error during login:', error);
            if (error.code === 'auth/invalid-credential') {
                setError('Error: Email or password is incorrect.');
            } else {
                setError('An error occurred. Please try again later.');
            }
        }
    };


    const handleForgetPasswordClick = () => {
        setResetPasswordClicked(true);
    };

    return (
        <div className="signin-form-container">
            <div className="signin-form">
                {resetPasswordClicked ? (
                    <ResetPassword onClose={() => setResetPasswordClicked(false)} />
                ) : (
                    <>
                        <h2>Sign In</h2>
                        <button className="close-btn" onClick={onClose}>X</button>
                        <form onSubmit={handleSubmit}>
                            <div className="form-group">
                                <label htmlFor="email">Email</label>
                                <input
                                    type="email"
                                    id="email"
                                    name="email_address"
                                    placeholder="Email"
                                    value={formData.email_address}
                                    onChange={handleInputChange}
                                    required
                                />
                            </div>

                            <div className="form-group">
                                <label htmlFor="password">Password</label>
                                <input
                                    type="password"
                                    id="password"
                                    name="Password"
                                    placeholder="Password"
                                    value={formData.Password}
                                    onChange={handleInputChange}
                                    required
                                />
                            </div>

                            {error && <div className="error-message">{error}</div>}

                            <button type="submit" className="submit-btn">Sign In</button>
                            <button type="button" className="reset-btn" onClick={handleForgetPasswordClick}>
                                Forget Password
                            </button>
                        </form>
                    </>
                )}
            </div>
        </div>
    );

};

export default SignInForm;